<template>
	<view class="register">
		<view class="formBox" style="margin-top: 20rpx;">
			<u-form :model="form" ref="uForm" :error-type="errorType">
				<view class="mybox">
					<u-upload :max-size="5 * 1024 * 1024" max-count="1" :action="action" :file-list="fileList"
						:header="header" @on-remove="onRemove" @on-success="onSuccess"></u-upload>
					<p style="font-size: 28rpx;font-weight: bold;color: #333333;"><span
							style="color:#FF0000;margin-right: 8rpx;">*</span> 请上传您的物品照片：</p>
				</view>
				<u-form-item label="物品名称" prop="goodsName" required label-width='180'>
					<u-input type="tel" v-model="form.goodsName" input-align="right" placeholder="请输入物品名称" />
				</u-form-item>
				<u-form-item label="物品规格" prop="untitled1" required label-width='180'>
					<u-input type="tel" v-model="form.untitled1" input-align="right" placeholder="请输入物品规格" />
				</u-form-item>
				<u-form-item label="物品数量" prop="untitled2" required right-icon="arrow-right" label-width='180'>
					<u-input v-model="form.untitled2" @click="sushe_show=true" :disabled="true" input-align="right"
						placeholder="请选择" />
				</u-form-item>
				<u-form-item label="领取时间" prop="untitled3" required right-icon="arrow-right" label-width='180'>
					<u-input v-model="form.untitled3" @click="timeshow=true" :disabled="true" input-align="right"
						placeholder="请选择" />
				</u-form-item>
			</u-form>
			<u-button @click="submit" type="primary" style="margin-top: 50rpx;">提交</u-button>
		</view>
		<!-- 选择器 -->
		<u-picker mode="time" v-model="timeshow" @confirm="time_confirm" :params="params"></u-picker>
		<u-select v-model="sushe_show" mode="single-column" :list="susheArr" @confirm="sushe_confirm"
			value-name='dictValue' label-name='dictLabel'></u-select>
		<u-toast ref="uToast"></u-toast>
	</view>
</template>

<script>
	import {
		addgoodsObj
	} from '../../api/index.js'
	import request from '@/api/request.js'
	export default {
		data() {
			return {
				imgList: [],
				header: {
					Authorization: "Bearer " + uni.getStorageSync("token"),
				},
				errorType: ['toast'],
				selectTips: null,
				params: {
					year: true,
					month: true,
					day: true,
					hour: true,
					minute: true,
					second: true
				},
				susheArr: [{
					dictValue: 1,
					dictLabel: 1
				}, {
					dictValue: 5,
					dictLabel: 5
				}, {
					dictValue: 10,
					dictLabel: 10
				}, {
					dictValue: 20,
					dictLabel: 20
				}, {
					dictValue: 50,
					dictLabel: 50
				}],
				timeshow: false,
				sushe_show: false,
				action: request.ROOTHTTP+"/file/upload",
				fileList: [],
				form: {
					untitled2:1,
				},
				rules: {
					goodsName: [{
						required: true,
						message: '请输入物品名称',
						trigger: ['change', 'blur'],
					}],
					untitled1: [{
						required: true,
						message: '请输入物品规格',
						trigger: ['change', 'blur'],
					}],
					untitled3: [{
						required: true,
						message: '请选择领取时间',
						trigger: ['change', 'blur'],
					}],
				}
			}
		},
		onReady() {
			this.$refs.uForm.setRules(this.rules);
		},
		onLoad() {
			this.form.userId = uni.getStorageSync('userInfo').userId;
		},
		methods: {
			onRemove(index) {
				this.imgList.splice(index, 1);
			},
			onSuccess(data, index, lists, name) {
				this.imgList.push(data.data.url);
			},
			time_confirm(e) {
				this.form.untitled3 = e.year + '-' + e.month + '-' + e.day + ' ' + e.hour +
					':' + e.minute + ':' + e.second;
			},
			sushe_confirm(e) {
				this.form.untitled2 = e[0].value;
			},
			submit() {
				if (!this.imgList[0]) {
					this.$refs.uToast.show({
						title: '请上传物品照片'
					});
				} else {
					this.form.file = this.imgList[0];
					this.$refs.uForm.validate(valid => {
						if (valid) {
							addgoodsObj(this.form).then(res => {
								if (res.code == 200) {
									this.$refs.uToast.show({
										title: '添加物品成功'
									});
									uni.navigateTo({
										url: 'things'
									})
								}
							});
						}
					});
				}
			}
		}
	}
</script>

<style scoped>
	.register {
		background-color: #F5F6FA;
		width: 100vw;
		height: 100vh;
	}

	.register /deep/ .u-list-item {
		width: 125rpx !important;
		height: 125rpx !important;
	}

	.register .title {
		width: 100%;
		text-align: center;
		font-size: 30rpx;
		font-weight: bold;
		color: #333333;
		padding: 32rpx 0 25rpx 0;
	}

	.yuanbox {
		position: absolute;
		right: 0;
	}

	.yuanbox /deep/ .u-list-item {
		width: 80rpx !important;
		height: 80rpx !important;
		border-radius: 50%;
	}

	.mybox {
		width: 100%;
		text-align: center;
	}

	.mybox /deep/ .u-upload {
		justify-content: center;
	}

	.formBox {
		padding: 0 30rpx 30rpx 30rpx;
		background-color: #fff;
	}

	.mititle {
		font-size: 30rpx;
		font-weight: bold;
		color: #333333;
		height: 90rpx;
		line-height: 90rpx;
		margin: 0 -30rpx;
		padding: 0 30rpx;
		border-bottom: 1px solid #f1f1f1;
	}

	.smtitle {
		width: 100%;
		text-align: center;
		font-size: 24rpx;
		color: #B1B1B1;
	}

	.smtitle b {
		color: #FF9D1D;
	}
</style>